import { Component,NgZone } from '@angular/core';
import { NavController,AlertController,Events, ModalController, Platform } from 'ionic-angular';
import { CountryProvider } from '../../providers/country/country';
import { ChannelProvider } from '../../providers/channel/channel';
import { Storage } from '@ionic/storage';

import * as moment from 'moment';

import { LocalNotifications } from '@ionic-native/local-notifications';

import { ConnectionProvider } from '../../providers/connection/connection';

import { AddChannelModal } from '../../modals/add-channel/add-channel';


import { Geolocation } from '@ionic-native/geolocation';
import { NativeGeocoder, NativeGeocoderReverseResult, NativeGeocoderForwardResult, NativeGeocoderOptions } from '@ionic-native/native-geocoder';
declare var cordova: any;

@Component({
  selector: 'page-prayer',
  templateUrl: 'prayer.html'
})
export class PrayerPage {
    
    
    public subscription:any;
    public cities:Array<any>;
    public regions:Array<any>;
    public channels:Array<any>;
    public prayerTimes:Array<any>;
    public region:string;
    public regionName:string;
    public cityName:string;
    public subscriptions:any;
    
    
    public loading:boolean;
    public channelLoading:boolean;
    public subscribedChannel:any;
    
    public user:any;
    
    public connectionStatus:boolean = true;
    
    public currentPosition:any;
    
    public settings:any;
    
    public alerts:Array<any>;
    
    constructor(public navCtrl: NavController, private geolocation: Geolocation, private nativeGeocoder: NativeGeocoder, public platform: Platform, public modalCtrl: ModalController, public countryProvider: CountryProvider, public channelProvider: ChannelProvider, private alertCtrl: AlertController, public storage: Storage, public events: Events, private localNotifications: LocalNotifications, private ngZone: NgZone, public connectionProvider:ConnectionProvider) {
        
        this.loading = true;
        this.channelLoading = true;
        this.subscription = {cityId:"", hubId:""};
        this.cities = [];
        this.regions = [];
        this.region = "";
        this.cityName = "";
        this.regionName = "";
        this.channels = [];
        this.prayerTimes = [];
        this.subscriptions = [];
        this.settings = {sound:"sound"};
        this.alerts = [];
        this.getCountries();
        
        this.currentPosition = {latitude:false,longitude:false};
        
        this.storage.get("settings").then((data) => {
            if (data){
                this.settings = data;
            }
        })
        
        this.storage.get("subscribedChannel").then((data) => {
            this.channelLoading = false;
            if (data){
                console.log(data);
                
                this.subscribedChannel = data;
                
                this.getSalatTimes(false);
            }
        });
        
        this.storage.get("subscription").then((data) => {
            if (data){
                console.log(data);
                
                this.subscription = data;
            }            
        })
        
        this.storage.get("subscriptions").then((data) => {
            if (data){
                console.log(data);
                
                this.subscriptions = data;
            }            
        })        
        
        this.user = {appUserName: "", email:"", mobile:"", password:""};

        this.storage.get("user").then((data) => {
            if (data){
                console.log(data);
                this.user.userId = data.userId;
                this.user.appUserName = data.appUserName;
                this.user.email = data.email;
                this.user.mobile = data.mobile;
                this.user.password = "";  
                
                this.getSubscriptions();
                
                        
            }  
        });        
        
        this.events.subscribe("mqtt:message", () => {
            this.getSalatTimes(true);            
        });
        
        this.connectionStatus = this.connectionProvider.getConnectionStatus();   
        
        this.events.subscribe("network:online", () => {
            this.getSalatTimes(false); 
            this.getCountries();
            this.ngZone.run(() => {
                this.connectionStatus = true;
            });            
        });

        this.events.subscribe("network:offline", () => {
            this.ngZone.run(() => {
                this.connectionStatus = false;
            });
        });
        
        this.events.subscribe("settings:updated", (data) => {
            this.settings = data
        });        

        
        this.platform.resume.subscribe(() => {
            this.ngZone.run(() => {
                this.connectionStatus = this.connectionProvider.getConnectionStatus();
            });
        }); 
        
        this.setupDailyNotification();
              
        
    }
    
  
    ionViewDidEnter(){
        this.getSalatTimes(false);
        this.ngZone.run(() => {
            this.connectionStatus = this.connectionProvider.getConnectionStatus();
        });
    }
    
    public setupDailyNotification(){
        if (this.platform.is("cordova")){
            cordova.plugins.notification.local.schedule({
              id: 2,
              title: 'Check your Salat times',
              text: 'Open the app to ensure you have the most up to date Salat times',
              smallIcon: 'res://small_icon',
              channel:"channel1",
              vibrate:true,
              trigger:{every: { hour: 17, minute: 0 }}
            });    
         
        }  
    }
    
    public setLocation(){
        this.geolocation.getCurrentPosition().then((resp) => {
            console.log(resp);
            this.currentPosition.latitude = resp.coords.latitude;
            this.currentPosition.longitude = resp.coords.longitude;
            this.nativeGeocoder.reverseGeocode(this.currentPosition.latitude, this.currentPosition.longitude, {useLocale:true, maxResults:1})
                .then((result: NativeGeocoderReverseResult[]) => {
                    console.log(result[0]);
                    let address = result[0];
                    
                    this.cityName = address["subLocality"];
                    this.regionName = address["locality"];
                    
                    for (let region of this.regions){
                        if (region.regionName === this.regionName){
                            this.region = region.regionId;
                            this.getCities();
                            for (let city of this.cities){
                                if (city.cityName === this.cityName){
                                    this.subscription.cityId = city.cityId;
                                }
                            }
                        }
                    }
                    
                })
                .catch((error: any) => console.log(error));            
        }).catch((error) => {
            console.log('Error getting location', error);
        });          
    }
    
    public getSubscriptions(){
        this.channelProvider.getSubscriptions(this.user.email).then((channels:Array<any>) => {
            console.log(channels);

            //check for duplicates
            var duplicates = [];
            var seenIds = [];
            for (var index in channels){
                if (channels[index].hub){
                    if (seenIds.indexOf(channels[index].hub.hubId) > -1){
                        console.log(channels[index].hub);
                        duplicates.push(index);
                    }
                    seenIds.push(channels[index].hub.hubId);
                }
            }
            for (let itemIndex of duplicates.reverse()){
                channels.splice(parseInt(itemIndex),1);
            }

            this.subscriptions = channels;
            this.storage.set("subscriptions", channels);
            if (!this.subscribedChannel && channels && channels.length > 0){
                this.subscribedChannel = channels[0].hub;
                this.subscription = {hubId:channels[0].hub.hubId, subscriberId: channels[0].subscriberId};
                this.storage.set("subscription", this.subscription);  
                this.storage.set("subscribedChannel", this.subscribedChannel);              
                this.events.publish("channel:subscribed", channels[0].hub.hubId);
                
                this.getSalatTimes(false);
            }
        });          
    }
    
    public getSalatTimes(isMqtt){
        if (this.subscribedChannel){
            this.channelProvider.getSalatTimes(this.subscribedChannel.hubId).then((times:any) => {
                console.log(times);
                this.loading = false;
                this.ngZone.run(() => {
                    this.prayerTimes = this.filterPrayers(times);      
                });
                
                
                this.generateNotifications();
                
                
                let latestSalatTimeId = 0;
                if (times && times.length > 0){
                    latestSalatTimeId = times[0].salatTimeId;
                }            
                
                console.log(latestSalatTimeId);    
                
                if (isMqtt){
                    this.generateMqttNotification(latestSalatTimeId, times);
                }else{
                    this.storage.set("salatTimes", times);
                    this.storage.set("latestSalatTimeId", latestSalatTimeId);
                }
                
                
                
            }).catch(() => {
                this.loading = false;
                this.storage.get("salatTimes").then((data) => {
                    if (data){
                        this.ngZone.run(() => {
                            this.prayerTimes = this.filterPrayers(data);
                        });
                        this.generateNotifications();
                    }
                });                
            });    
        }    
    }
    
    private filterPrayers(prayerTimes){
        let filteredPrayers = [];

        for (let prayer of prayerTimes){
            let prayerExpiry = moment(prayer.expireDateTime, 'DD-MM-YYYY');
            let prayerStart = moment(prayer.activateDateTime, 'DD-MM-YYYY');
            let today = moment();

            if (prayerExpiry.isAfter(today)  && prayerStart.isBefore(today)){
                filteredPrayers.push(prayer);
            }
        }
        
        return filteredPrayers;        
    }  
    
    private generateMqttNotification(latestSalatTimeId, newTimes){
        console.log(latestSalatTimeId);

        this.storage.get("latestSalatTimeId").then((id) => {
            console.log(id);
            if (id !== latestSalatTimeId){
                //is an update salat time
                this.doSalatTimesChangedAlert();
                
                this.storage.set("latestSalatTimeId", latestSalatTimeId)
                this.storage.set("salatTimes", newTimes);
            }
            else{
                
                
                //check to see if any salat times have changes
                this.storage.get("salatTimes").then((oldTimes) => {
                    
                    
                    oldTimes = oldTimes ? oldTimes : [];
                    newTimes = newTimes ? newTimes : [];
                    
                    console.log(oldTimes);
                    console.log(newTimes);
                    let timesChanged = false;
                    for (let newTime of newTimes){
                        for (let oldTime of oldTimes){
                            if (newTime.salatTimeId === oldTime.salatTimeId){
                                console.log("equals");
                                for (var index in oldTime){
                                    if (oldTime[index] !== newTime[index]){
                                        timesChanged = true;
                                        break;
                                    }
                                }
                            }
                        }
                    } 
                
                    if (timesChanged){
                        this.doSalatTimesChangedAlert();
                    }
                    else{
                        //is an announcement
                        this.doAnnouncementAddedEvent();
                    }
                    
                    this.storage.set("salatTimes", newTimes);

                });
                this.storage.set("latestSalatTimeId", latestSalatTimeId);  
            } 
            
        });

             
    }  
    
    private doSalatTimesChangedAlert(){
        this.localNotifications.schedule({
          id: 1,
          title: 'Update Received',
          text: 'An update to your Salat times has been made.',
          smallIcon: 'res://small_icon',
          channel:"channel1",
          vibrate:true
        });  
        this.dismissAlerts();              
        setTimeout(() => {
            //alert("An update to your Salat times has been made.")
            let alert = this.alertCtrl.create({
              title: 'Update Received',
              message: 'An update to your Salat times has been made.',
              buttons: ['Dismiss']
            });
            this.alerts.push(alert);
            alert.present();                    

        },500);        
    }
    
    private doAnnouncementAddedEvent(){
        this.localNotifications.schedule({
          id: 1,
          title: 'Update Received',
          text: 'A new announcement has been made.',
          smallIcon: 'res://small_icon',
          channel:"channel1",
          vibrate:true
        });   
        this.dismissAlerts();              
        setTimeout(() => {
            //alert("A new announcement has been made.")
            
            let alert = this.alertCtrl.create({
              title: 'Update Received',
              message: 'A new announcement has been made.',
              buttons: ['Dismiss']
            });
            alert.present();                        
        },500);        
    }
    
    private dismissAlerts(){
        console.log('Dismissed alert');
        if (this.alerts.length) {
            this.alerts.forEach(e => {
                e.dismiss();
            });
        }
        this.alerts = [];     
    }
    
    
    
    private generateNotifications(){
        console.log(this.prayerTimes);

        if (!this.platform.is("cordova")){
            return;
        }

        this.localNotifications.getScheduledIds().then((ids) => {
            let notPrayerTime = ids.indexOf(1);
            if (notPrayerTime > -1){
                ids.splice(notPrayerTime,1);
            }
            
            notPrayerTime = ids.indexOf(2);
            if (notPrayerTime > -1){
                ids.splice(notPrayerTime,1);
            }                      
            
            this.localNotifications.cancel(ids).then(() => {
                this.scheduleNotifications();
            }).catch(() => {
                this.scheduleNotifications();
            });
               
            

        }).catch(() => {
            this.scheduleNotifications();
        });
    }
    
    private scheduleNotifications(){
            let id = 3;

            for (let prayer of this.prayerTimes){
                let prayerTime = moment((prayer.activateDateTime + " " + prayer.adzanTime), 'DD-MM-YYYY hh:mm A');
                let prayerTime2 = moment((prayer.activateDateTime + " " + prayer.ikhamaTime), 'DD-MM-YYYY hh:mm A');

                if (prayer.autoTimeUp && prayer.autoTimeUp > 0){
                    prayerTime.add(prayer.autoTimeUp, 'minutes');
                    prayerTime2.add(prayer.autoTimeUp, 'minutes');
                }

                let prayerExpiry = moment((prayer.expireDateTime + " " + prayer.adzanTime), 'DD-MM-YYYY hh:mm A');
                let days = prayerExpiry.diff(prayerTime, 'days') + 1;
                let today = moment();

                let sound = "";
                if (prayer.prayerName === "Fajar" && (this.settings && this.settings.sound !== "default")){
                    sound = 'file://assets/audio/Azan-S.mp3';
                }
                else if (prayer.prayerName !== "Tahajjud" && prayer.prayerName !== "Chasht" && (this.settings && this.settings.sound !== "default")){
                    sound = 'file://assets/audio/Azan-A.mp3';
                }            

                let channel = ((prayer && prayer.prayerName) ?  prayer.prayerName.toLowerCase().split(' ').join('') : "prayer");
                

                for (let i = 0; i < days; i++){
                    if (prayerTime.isAfter(today)){
                        this.localNotifications.schedule({
                            id:id,
                            title: prayer.prayerName + " prayer (Adzan)",
                            text: "Starts at " + prayer.adzanTime.trim(),
                            smallIcon: 'res://small_icon',
                            sound: sound,
                            foreground: true,
                            vibrate:true,
                            channel:channel,
                            trigger: {at: new Date(prayerTime.year(), prayerTime.month(), prayerTime.date(), prayerTime.hour(), prayerTime.minutes())}
                        }); 

                        id++
                    }


                    if (prayerTime2.isAfter(today)){
                        this.localNotifications.schedule({
                            id:id,
                            title: prayer.prayerName + " prayer (Ikhama)",
                            text: "Starts at " + prayer.ikhamaTime.trim(),
                            smallIcon: 'res://small_icon',
                            sound: sound,
                            foreground: true,
                            vibrate:true,
                            channel:channel,
                            trigger: {at: new Date(prayerTime2.year(), prayerTime2.month(), prayerTime2.date(), prayerTime2.hour(), prayerTime2.minutes())}
                        }); 
                        id++
                    }     


                    prayerTime.add(1,'days');    
                    prayerTime2.add(1,'days');     
                }
                
                

                console.log(prayerTime);
            }        
    }
    
    public getCountries(){
        this.countryProvider.getCountries().then((data: Array<any>) => {
            console.log(data[0].regionsList);
            this.regions = data[0].regionsList;
            this.setLocation(); 
        }).catch(() => {
            
        })
    }
    
    public getCities(){
        for (let region of this.regions){
            console.log(region.regionId);
            console.log(this.region);
            if (parseInt(region.regionId) === parseInt(this.region)){
                console.log(region.citiesList);
                this.cities = region.citiesList;
            }
        }
    }
    
    public getChannels(){
        
        this.channels = [];
        
        
        this.channelProvider.getChannels(this.subscription.cityId).then((data:Array<any>) => {
            console.log(data);
            this.channels = data;
        }).catch(() => {
            
        })
        
    }
    
    public selectChannel(channel){
        this.subscription.hubId = channel.hubId;
        let alert = this.alertCtrl.create({
          title: 'Subscribe',
          message: 'Are you sure you want to subscribe to this channel?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel'
            },
            {
              text: 'Yes',
              handler: () => {
                  this.storage.set("subscription", this.subscription);
                  console.log(channel);
                  this.storage.set("subscribedChannel", channel);
                  
                  setTimeout(() => {
                    this.subscribedChannel = channel;
                  },500);

                  
                  let subscription = {subscriberId:0, hub:channel};
                  this.subscriptions.push(subscription);
                  
                  this.channelProvider.subscribe(this.user.email, channel.hubId).then((data) => {
                      console.log(data);
                      let id = data["subscriberId"];
                      this.subscription.subscriberId = id;
                      subscription.subscriberId = id;
                      this.storage.set("subscriptions", this.subscriptions);
                      this.storage.set("subscription", this.subscription);
                      this.events.publish("channel:subscribed", channel.hubId);
                      
                      this.getSalatTimes(false);
                  }).catch(() => {
            
                    });
                    
                    
                    //make sure app can run in the background
                    if (this.platform.is("cordova")){
                        cordova.plugins.DozeOptimize.IsIgnoringBatteryOptimizations(function (response){
                            console.log("IsIgnoringBatteryOptimizations: "+response);
                            if(response=="false"){
                                cordova.plugins.DozeOptimize.RequestOptimizations(function (response){
                                    console.log(response);
                                }, function (error){
                                    console.error("BatteryOptimizations Request Error"+error);			
                                });
                            }
                            else{
                                console.log("Application already Ignoring Battery Optimizations");
                            }		
                            }, function (error){
                                console.error("IsIgnoringBatteryOptimizations Error"+error);    
                            });           
                    }                    
              }
            }
          ]
        });
        alert.present();              
    }
    
    public changeSubscribedChannel(subscription){
        
        this.events.publish("channel:unsubscribed", this.subscription.hubId);
        
        let channel = subscription.hub;
        this.subscription.hubId = channel.hubId;
        this.subscription.subscriberId = subscription.subscriberId;
        this.subscription.hub = channel;
        this.subscribedChannel = channel;
        this.storage.set("subscribedChannel", channel);
        this.storage.set("subscription", this.subscription);
        this.prayerTimes = [];
        this.getSalatTimes(false);
        this.events.publish("channel:subscribed", channel.hubId);
        
    }
    
    public openAddChannel(){
        let modal = this.modalCtrl.create(AddChannelModal, {subscriptions: this.subscriptions, region: this.region, city: this.subscription.cityId}); 
        modal.onDidDismiss(data => {
            console.log(data);
            if (data){
                let channel = data;

                this.channelProvider.subscribe(this.user.email, channel.hubId).then((data) => {
                    console.log(data);
                    let id = data["subscriberId"];
                    let subscription = {subscriberId:id, hub:channel };
                    this.subscriptions.push(subscription);
                    this.storage.set("subscriptions", this.subscriptions);
                }).catch(() => {

                  })                
                
                
            }
        });        
        modal.present();          
      
    }
    
    
    public unsubscribe(){
        let alert = this.alertCtrl.create({
          title: 'Unsubscribe',
          message: 'Are you sure you want to unsubscribe from this channel?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel'
            },
            {
              text: 'Yes',
              handler: () => {
                  
                  this.loading = true;
                  this.storage.remove("subscription");
                  this.storage.remove("subscribedChannel");
                  
                  this.subscribedChannel = null;
                  let id = this.subscription["subscriberId"];
                  let hubId = this.subscription["hubId"];
                  
                  console.log(id);
                  console.log(this.subscription);
                  
                  this.channelProvider.unsubscribe(id).then((data) => {
                      console.log(data);
                      this.events.publish("channel:unsubscribed", hubId);
                      this.subscription = {cityId:"", hubId:""};
                      this.getSubscriptions();
                  }).catch(() => {
                      this.getSubscriptions();  
                    });
              }
            }
          ]
        });
        alert.present();              
    }    

}



